
.App {
  text-align: center;
  vertical-align: middle;
}
.full{
  height: 100%;
}
.blue{
  background-color: blue
}
.red{
  background-color: red
}
#Toppage{
  color: white
}
.Pages{

}

.Container{
  position: relative;
  top: 14%;
  height: 80%;
}

.rect{
  display: inline-block;
  text-align: center;
  margin: auto;
  width: 50%;

}

.miniRect{
  position: relative;
  display: inline-block;
  margin: 0;
  width: 500px;
  height:300px;
}

.green {
  background-color: green
}

.App-logo {
  margin-top: -5.97%;
  width: 18%;
}

.App-mini{
  position: relative;
  -webkit-transition : 300ms ease;
  -o-transition : 300ms ease;
  transition : 300ms ease;
  width: 7%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 2em;
  margin-bottom: 2%;
  font-weight: 700;
}

.App-intro {
  margin-top:10%;
  vertical-align: bottom;
  font-size: 12pt;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



